	module.exports = {"data":{"post":{"html":"<p>Maybe public life will get back to normal in the next day or two, now that the World Cup is over with Brazil defeating Germany 2-0. A just result. Germany played their best match so far, controlling midfield, but were unable to penetrate the box and left openings for Brazil at their own end. Yesterday's match for 3rd place (Turkey over South Korea 3-2) was more entertaining, but I guess our boys at <a href=\"http://www.worldcupblog.com\">World Cup Blog</a> were asleep for that one.</p>\n<p>(I'm still getting my templates here straightened out. Updating my daily archive is timing out before completion, and I'm trying to find a way around that. Until I do, the referals page will also be incomplete.)</p>","fields":{"title":"Penta","date":"30 Jun 2002","url":"https://papascott.de/2002-06-30-penta/"}}},"pathContext":{"slug":"/2002-06-30-penta/","prev":{"fields":{"title":"Blog Hosed, Name In Lights","date":"29 Jun 2002","slug":"/2002-06-29-blog-hosed-name-in-lights/"}},"next":{"fields":{"title":"When In Doubt. Punt...","date":"02 Jul 2002","slug":"/2002-07-02-when-in-doubt-punt/"}}}}

